@import "../../../tools/tools.scss";

html {
  background: #3d405b;
}

.serv-sub-comp {
  width: 100vw;
  height: 500vh;
  display: flex;
  flex-direction: column;
  position: relative;

  &.active * {
    animation: fadeMainActive 2s !important;
  }
  & * {
    animation: fadeServ 2s forwards;
  }
  & > .background {
    opacity: 1;
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    background: radial-gradient(circle at top right, #ececec, #848484 89%);
    animation: backOpacity 2s forwards;
  }
  .title {
    display: flex;
    flex-direction: column;
    margin: auto;
    padding: 1vw 0 2vh 0;
    width: 100vw;
    z-index: 2;
    border-radius: 20px;
    .title-circle {
      flex-grow: 1;
      svg {
        width: 10vw;
        path {
          fill: transparent;
          stroke: mistyrose;
        }
        text {
          fill: mistyrose;
          letter-spacing: 5px;
        }
      }
    }
    .description {
      font: 1.5vw Julius Sans One;
      background: gold;
      width: 32vw;
      margin: auto;
      border-radius: 11px;
      padding: 1vw;
      position: relative;
    }
  }
  .panels {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    z-index: 1;
    margin-top: 20vw;
    .frontend {
      margin: 0vh 5vw 0 0;
      .background {
        margin-right: 10vw;
      }
      .filter {
        background: #084c61;
        transform: skewY(10deg);
        .slider {
          left: 18vw;
        }
      }
    }
    .backend {
      margin-top: 22vw;
      flex-direction: row-reverse;
      .background {
        margin-left: 8vw;
      }
      .filter {
        background: #2f4c45;
        transform: skewY(-10deg);
        .slider {
          right: 10vw;
        }
      }
    }
    .design {
      margin: 22vw 0;
      .filter {
        background: #f4a434;
        transform: skewY(10deg);
      }
      .background {
        margin-right: 10vw;
      }
      .slider {
        left: 18vw;
      }
    }
    & > div {
      width: 100vw;
      height: 100vh;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      position: relative;
      .filter {
        display: flex;
        width: 100vw;
        height: 60vh;
        position: absolute;
        left: 0;
        top: 13vh;
        z-index: 1;
        box-shadow: 0 0 20px black;
        overflow: hidden;
        .slider {
          width: 4vw;
          height: 100vh;
          display: flex;
          flex-direction: column;
          position: absolute;
          z-index: 1;
          writing-mode: vertical-rl;
          color: #848181;
          & > div {
            border-left: solid;
            font: 1.5vw Cinzel;
          }
          .slider-one {
            position: relative;
            div {
              word-spacing: 8vw !important;
              animation: marqueeOne 18s linear infinite;
            }
          }
          .slider-two {
            word-spacing: 5vw;
            div {
              animation: marqueeTwo 14s linear infinite;
            }
          }
          .slider-three {
            word-spacing: 13vw !important;
            div {
              animation: marqueeOne 20s linear infinite;
            }
          }
        }
      }
      .title-wrap {
        margin: 10vh 3vw;
        padding: 5vh 2vw;
        align-self: baseline;
        border-bottom: solid;
        display: flex;
        color: #848181;
        justify-content: space-around;
        flex-direction: column;
        width: 34vw;
        height: 35vh;
        text-align: center;
        border-radius: 4px;
        z-index: 2;
        .title {
          display: flex;
          flex-direction: column;
          width: 100%;
          z-index: 2;
          font: 3vw Julius Sans One;
        }
        .description {
          width: 100%;
          font: 1vw Museo Moderno;
          text-align: center;
        }
      }
      .background {
        width: 22vw;
        height: 70vh;
        background-size: cover;
        box-shadow: 0 0 16px;
        z-index: 2;
      }
    }
  }
}

@keyframes fadeMainActive {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes backOpacity {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeServ {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes marqueeOne {
  0% {
    transform: translate(0, -100%);
  }
  100% {
    transform: translate(0%, 60%);
  }
}
@keyframes marqueeTwo {
  0% {
    transform: translate(0%, 60%);
  }
  100% {
    transform: translate(0%, -100%);
  }
}
