@mixin makeBurger($animClass) {
  position: absolute;
  width: 3vh;
  height: 3vh;
  padding: 0;
  border: none;
  background: transparent;
  span {
    position: absolute;
    background: black;
    width: 100%;
    height: 20%;
    display: block;
    border-radius: 10px;
    &:nth-child(1) {
      top: 0.5vh;
      transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
        transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
    }
    &:nth-child(3) {
      bottom: 0;
      transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
        transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
    }
  }
  &.#{$animClass} span {
    &:nth-child(1) {
      top: 1.5vh;
      transform: rotate(45deg);
      transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1),
        transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
    }
    &:nth-child(2) {
      background: transparent;
    }
    &:nth-child(3) {
      bottom: 1vh;
      transform: rotate(-45deg);
      transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1),
        transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
    }
  }
}

@mixin borderFromCent($parentToo, $hoverBorder, $parentBorder: "antiquewhite") {
  @if $parentToo {
    box-shadow: inset 0 0 0 2px $parentBorder;
  }
  position: relative;
  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 0;
    transform-origin: center;
    left: 0;
  }
  &:before {
    top: -0.6vh;
    width: 4vw;
    height: 17vh;
    transform: scale3d(0, 1, 1);
    border-top: 5px solid $hoverBorder;
    border-bottom: 5px solid $hoverBorder;
  }
  &:after {
    width: 97%;
    height: 100%;
    transform: scale3d(1, 0, 1);
    border-left: 5px solid $hoverBorder;
    border-right: 5px solid $hoverBorder;
  }
  &:hover:before,
  &:hover:after {
    transform: scale3d(1, 1, 1);
    transition: transform 0.5s;
  }
}

@mixin makeCircle($size) {
  width: #{$size}vw;
  height: #{$size}vw;
  border-radius: 50%;
}

@mixin makeScrollIcon($width, $height, $color) {
  width: $width;
  height: $height;
  border: 2px solid $color;
  border-radius: 24px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  &:after {
    content: "";
    position: absolute;
    top: 5px;
    left: 50%;
    margin-left: -2px;
    background-color: $color;
    border-radius: 100%;
    width: 4px;
    height: 4px;
    opacity: 1;
    transform: translateY(0px) scaleY(1) scaleX(1) translateZ(0px);
    animation: scroll 1.5s -1s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
  }
}

@mixin makeHexagon() {
  position: relative;
  width: 300px;
  height: 173.21px;
  background-color: #64c7cc;
  margin: 86.6px 0;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  & span {
    display: block;
    position: absolute;
    top: 0px;
    left: 0;
    width: 300px;
    height: 173.2051px;
    z-index: 2;
    background: inherit;
  }
  &:before,
  &:after {
    content: "";
    position: absolute;
    z-index: 1;
    width: 212.13px;
    height: 212.13px;
    -webkit-transform: scaleY(0.5774) rotate(-45deg);
    -ms-transform: scaleY(0.5774) rotate(-45deg);
    transform: scaleY(0.5774) rotate(-45deg);
    background-color: inherit;
    left: 43.934px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  }
  &:before {
    top: -106.066px;
  }

  &:after {
    bottom: -106.066px;
  }
}

@mixin makeBrokenText($content) {
  p {
    position: relative;
    transition: color 0.5s;
    &:before,
    &:after {
      content: $content;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      color: transparent;
      transition: 0.5s;
    }
    &:before {
      clip-path: polygon(0% 0%, 100% 100%, 100% 0%);
    }
    &:after {
      clip-path: polygon(90% 100%, 0% 100%, 0% 0%);
    }
  }
  &:hover {
    p {
      color: transparent;
      &:before,
      &:after {
        color: #ea3f5f;
        font-weight: bolder;
      }
      &:before {
        transform: translate3d(0px, -2px, 0);
      }
      &:after {
        transform: translate3d(0px, 2px, 0);
      }
    }
  }
}

@mixin importFonts($fontName...) {
  @each $name in $fontName {
    @font-face {
      font-family: $name;
      src: local($name), url("../assets/fonts/#{$name}.ttf") format("truetype");
    }
  }
}

@keyframes scroll {
  0%,
  20% {
    transform: translateY(0px) scaleY(1) scaleX(1) translateZ(0px);
  }
  10% {
    transform: translateY(0px) scaleY(1.2) scaleX(1.2) translateZ(0px);
    opacity: 1;
  }

  100% {
    transform: translateY(20px) scaleY(2.5) scaleX(0.5) translateZ(0px);
    opacity: 0.01;
  }
}
