.App {
  text-align: center;
  .cursor,
  .cursor-border {
    position: fixed;
    z-index: 100;
    pointer-events: none;
    border-radius: 100%;
    backface-visibility: hidden;
  }
  .cursor-border {
    width: 2vw;
    height: 2vw;
    border: 2px solid #151515;
    transform: translate(-50%, -50%);
    transition: left 0.2s linear, top 0.2s linear;
    mix-blend-mode: color-burn;
    background: #ee82eecc;
  }
  .cursor {
    width: 1vw;
    height: 1vw;
    background: black;
    border: none;
    margin: -0.5% -0.5%;
    transition: left 0.1s linear, top 0.1s linear;
  }
}
