.slide {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  opacity: 0;
  transition: 2s;
  visibility: hidden;
  .logo {
    display: flex;
    width: 30vw;
    height: 15vh;
    align-items: center;
    justify-content: space-evenly;
    font: 2vw "Kaushan Script", cursive;
    text-shadow: 0px 0px 10px;
    letter-spacing: 1px;
    .text {
      width: 80%;
      position: relative;
      overflow: hidden;
      p {
        position: relative;
        top: 0;
        right: -21vw;
      }
    }
    img {
      width: 15%;
      border-radius: 15px;
      box-shadow: 0px 0px 14px 2px;
    }
  }
}
