.about-sub-comp {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  .logo {
    color: aliceblue;
  }
  canvas {
    max-width: 100%;
    display: block;
  }

  .panels {
    flex-grow: 3;
    display: flex;
    z-index: 2;
    pointer-events: none;
    & > div {
      flex-grow: 1;
    }
    .info {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      width: 35vw;
      color: aliceblue;
      div {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        text-align: left;
        margin-left: 8vw;
      }
      .title {
        font: 2.5vw Julius Sans One;
        p {
          margin: 0;
        }
      }
      .desc {
        font: 1.5vw Julius Sans One;
        margin-bottom: 4vh;
        width: 38vw;
      }
    }
    .contact-form {
      display: flex;
      align-items: center;
      justify-content: space-around;
      backdrop-filter: blur(8px);
      background: #3d405b73;
      margin: 2vw;
      border-radius: 15px;
      box-shadow: 0 0 25px black;
      .form-wrapper {
        pointer-events: all;
        margin-bottom: 10vh;
        width: 45%;
        height: 45%;
        padding: 3vw;
        border-radius: 15px;
        z-index: 1;
        p {
          margin-top: 5vh;
          font: 0.8vw Julius Sans One;
          color: aliceblue;
        }
        form {
          display: flex;
          flex-direction: column;
          color: whitesmoke;
          width: 21vw;
          & > * {
            margin-bottom: 1vh;
            font: 1.2vw Museo Moderno;
            background: transparent;
            border: none;
            color: aliceblue;
          }
          .email {
            border-bottom: solid !important;
          }
          label {
            margin-bottom: 3vh !important;
            font-size: 2vw !important;
          }
          .subject {
            height: 15vh;
            border: none;
            border-bottom: solid;
          }
          button {
            margin-top: 3vh;
            padding: 0.5vw;
            border-radius: 8px;
            background: #457b9d8f;
            border: none;
            box-shadow: 0 0 11px black;
            color: aliceblue;
          }
        }
      }
      .social-contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 1.5vw;
        .line {
          height: 35vh;
          width: 0.1vw;
          background: aliceblue;
          margin-bottom: 5vh;
          border-radius: 10px;
        }
        a {
          margin-bottom: 2vh;
          color: aliceblue;
        }
        p {
          writing-mode: tb-rl;
          margin-top: 5vh;
          color: aliceblue;
          text-transform: capitalize;
          font: 1.5vw Julius Sans One;
        }
      }
    }
  }
}
