.projects-sub-comp {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  & * {
    animation: fadeServ 3s forwards;
  }
  .background {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    background: radial-gradient(circle at top right, #ffffff, #c7c7c7 89%);
    animation: backOpacityProj 2s forwards;
    z-index: -1;
  }
  .main {
    width: 50vw;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: 15vw;
      height: 15vw;
    }
    p {
      font: 1vw Kaushan Script;
    }
  }
}

@keyframes backOpacityProj {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
