@import "../../tools/tools.scss";

.aside-wrapper {
  width: 3vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  transition: width 0.5s, left 0.5s;
  .opacity-layer {
    width: 0;
    height: 100vh;
    position: absolute;
    z-index: 2;
    transition: background 0.5s;
    background: transparent;
  }
  .aside {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    background: #2e3c45;
    box-shadow: 0px 0px 7px;
    position: relative;
    z-index: 3;
    .bars {
      position: relative !important;
      margin: 3.2vh auto;
      transition: margin 0.5s;
      @include makeBurger("animate");
      z-index: 3;
      span {
        position: absolute;
        background: #ea3f5f;
        width: 100%;
        height: 20%;
        display: block;
        border-radius: 10px;
      }
      &:hover span {
        background: #f8f2d2;
        transition: background 0.2s;
      }
    }
    .menu-content {
      writing-mode: vertical-rl;
      list-style: none;
      color: aliceblue;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: 55%;
      margin: 0;
      transition: 0.5s;
      font: 0.9vw "Julius Sans One";
      .current {
        p {
          color: transparent;
          &:before,
          &:after {
            color: #ea3f5f;
            font-weight: bolder;
          }
          &:before {
            transform: translate3d(0px, -2px, 0);
          }
          &:after {
            transform: translate3d(0px, 2px, 0);
          }
        }
      }
      li {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        transition: background 0.5s;
        @include makeBrokenText(attr(data-letters));
        a {
          color: antiquewhite;
          text-decoration: none;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  &.active {
    width: 15vw;
    & .opacity-layer {
      width: 100vw;
      background: #00000087;
    }
    & .aside {
      justify-content: initial;
      & .bars {
        margin: 3.2vh 0vw 0vh 70%;
        transition: margin 0.5s;
      }
      & .menu-content {
        width: 60%;
        height: 40vh;
        margin-top: 15vh;
        writing-mode: unset;
        flex-direction: column;
        li {
          &:not(.current) {
            p {
              border-bottom: 4px solid #ea3f5f;
            }
          }
          a {
            p {
              width: 100%;
              text-align: left;
              padding-bottom: 1vh;
            }
          }
        }
      }
      & .chat {
        background: #2e3c45;
      }
    }
  }
}
