@import "../../../tools/tools.scss";

.welcome-sub-comp {
  background-image: radial-gradient(circle at top right, #ececec, #848484 89%);
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: space-between;
  align-items: flex-end;

  &.active * {
    animation: fadeMainActive 4s !important;
  }
  & * {
    animation: fadeMain 2s normal;
  }
  .filter {
    transform-origin: 50% 50%;
    transform: scale(100, 100);
    position: absolute;
    width: 35vw;
    height: 240vh;
    transform: rotateZ(60deg);
    left: 30vw;
    background: #ea3f5f;
    z-index: 1;
    bottom: -42vw;
    box-shadow: black 0px 0px 19px 2px;
    animation: filter 1s;
  }
  .img-layer {
    display: flex;
    width: 40vw;
    height: 100vh;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    z-index: 2;
    svg {
      width: 35vw;
      position: absolute;
      bottom: 16vh;
      z-index: 1;
      word-spacing: 4px;
      letter-spacing: 2px;
      transition: width 0.5s;
      text {
        font-size: 0.6vh;
        font: 0.2vw "Julius Sans One";
        font-weight: 900;
        text-transform: uppercase;
        fill: darkslategray;
      }
      path {
        fill: transparent;
      }
    }
    img {
      filter: grayscale(1) drop-shadow(2px 4px 6px black);
      width: 30vw;
      z-index: 2;
      transition: width 0.5s;
    }
    canvas {
      position: absolute;
    }
  }
  .more-wrapper {
    display: flex;
    justify-content: center;
    align-items: inherit;
    height: 100vh;
    position: relative;
    z-index: 2;
    background: transparent;
    border: none;
    font-size: 7vh;

    .more {
      &:hover {
        color: antiquewhite;
        transition: 0.3s;
        .scroll-icon {
          transition: 0.3s;
          border-color: antiquewhite;
          &:after {
            background-color: antiquewhite;
          }
        }
      }
    }
    .scroll-icon {
      @include makeScrollIcon(1.5vw, 5vh, black);
      cursor: none !important;
    }
    p {
      font: 2vh "Julius Sans One";
      text-orientation: upright;
      writing-mode: tb-rl;
      margin-bottom: 9vh;
      font-weight: bold;
    }
  }
  .text-layer {
    height: 65vh;
    width: 25vw;
    text-align: center;
    margin: 0vh 10vw 15vh 10vw;
    z-index: 1;
    border: solid;
    box-shadow: 2px 2px 13px;
    transition: margin 0.3s;
    .title {
      display: table-caption;
      font: 4vw "Julius Sans One";
      font-weight: bold;
      text-align: left;
      margin: 15vh 0 0vh 0;
      text-shadow: black -3px 0px 8px;
    }
    .sub-title {
      font: 1vw "Julius Sans One";
    }
  }
  .social-contact {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 6vw;
    height: 25vh;
    font-family: "Julius Sans One";
    border-bottom: solid;
    margin: 0 2vw 2vh 0;
    a {
      transition: color 0.3s;
      color: black;
      &:hover {
        color: crimson;
      }
    }
    svg {
      font-size: 2.2vh;
    }
    p {
      writing-mode: vertical-lr;
      font-size: 1.2vw;
      font-weight: bold;
      text-shadow: 0px 0px 2px;
    }
  }
}

@keyframes filter {
  0% {
    transform: rotateZ(0deg);
    transform: scaleX(0);
  }
  50% {
    transform: scaleX(1);
  }
  100% {
    transform: rotateZ(60deg);
  }
}

@keyframes fadeMain {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeMainActive {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
}
