.main-page {
  .roll {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    background: transparent;
    .logo {
      position: relative;
      left: 40vw;
      top: 50vh;
    }
  }
  .cursor {
    pointer-events: none;

    &__ball {
      position: fixed;
      top: 0;
      left: 0;
      mix-blend-mode: difference;
      z-index: 1000;

      circle {
        fill: #f7f8fa;
      }
    }
  }

  .number {
    position: absolute;
    right: 4vw;
    top: -85px;
    z-index: 2;
    margin: 0;
    font: 30vh "Niconne", cursive;
    font-style: oblique;
    color: #ffb0b070;
  }
}
